import React, { Component } from 'react'
import Moveable from 'react-moveable'
import DraggableContext from '../context/DraggableContext'


export default class Draggable extends Component {
  static contextType = DraggableContext

  constructor(props) {
    super(props)
    this.state = {
      target: null,
    }
  }

  componentDidMount() {
    const { id } = this.props
    const el = document.querySelector(`.draggable-${id}`)
    this.setState({
      target: el,
    })
  }

  render() {
    const {
      children,
      id,
    } = this.props
    const { draggable } = this.context
    const klass = `draggable draggable-${id}`

    return (
      <>
        <div className={draggable ? `${klass} draggable--enabled` : klass}>
          {children}
        </div>
        <Moveable
          target={this.state.target}
          container={null}
          origin
          draggable
          throttleDrag={0}
          onDrag={({ target, transform }) => {
              if (draggable) {
                target.style.transform = transform;
              }
          }}
          resizable={false}
          scalable={false}
          rotatable={false}
          warpable={false}
        />
      </>
    )
  }
}
