import React, { Component } from 'react'
import { Link } from 'gatsby'
import config from '../../data/SiteConfig'

export default class Footer extends Component {
  render() {
    return (
      <footer className="footer container">
        <a href={`https://twitter.com/${config.userTwitter}`} target="_blank" rel="noopener noreferrer">
          Twitter
        </a>
        <a href={`https://github.com/${config.userGithub}`} target="_blank" rel="noopener noreferrer">
          GitHub
        </a>
        <a href={`${config.siteUrl}/rss.xml`} target="_blank" rel="noopener noreferrer">
          RSS
        </a>
        <a
          href={config.repo}
          target="_blank"
          rel="noopener noreferrer"
        >
          Source
        </a>
      </footer>
    )
  }
}
