  repo: 'https://github.com/agm-dev/gatsby-agm-dev-blog',
  pathPrefix: '',
  dateFromFormat: 'YYYY-MM-DD',
  dateFormat: 'MMMM Do, YYYY',
  siteDescription: 'Adrián Gonzalo is a full-stack developer focused on Javascript technologies.',
  siteRss: '/rss.xml',
